body,
html,
#root {
  font-size: 16px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: #3282b8;
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
}
p {
  margin-top: 30px;
}
a {
  text-decoration: none;
  color: white;
}
h1 {
  color: whitesmoke;
  text-transform: uppercase;
  text-align: center;
}
h1 i {
  color: aliceblue;
}
i {
  color: rgb(162, 173, 0);
}
