.card {
  margin: 8px;
  background: #0f4c75;
  width: 40%;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 4px #000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-items: center;
}
.category {
  color: #46535a;
  letter-spacing: 1.5px;
  font-style: italic;
  text-align: right;
}
.text {
  color: #000;
  font-size: 1.3em;
  letter-spacing: 2px;
  word-wrap: break-word;
}
.list {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-left: -37px;
  justify-content: center;
  text-align: center;
}
.listItem {
  width: 330px;
  margin: 2px 0;
  color: #444;
  background: #bbe1fa;
  border-radius: 8px;
  padding: 20px;
  word-wrap: break-word;
}
.button {
  color: #fefefe;
  background-color: rgb(4, 28, 58);
  font-size: 1.2em;
  padding: 8px 20px;
  border-radius: 8px;
  border: 2px solid rgb(255, 255, 255);
  outline: none;
  transition: ease all 0.4s;
  margin-top: 20px;
  cursor: pointer;
}

@media only screen and (max-width: 970px) {
  .card {
    width: 50%;
  }
  .listItem {
    width: 300px;
  }
}
@media only screen and (max-width: 768px) {
  .card {
    width: 300px;
  }
  .listItem {
    width: 280px;
  }
}
